import { useMemo, useRef } from 'react'
import { useThree, extend, useFrame } from '@react-three/fiber'
import { random } from 'canvas-sketch-util'
import * as THREE from 'three'

const Star = ({ position, color }) => {
  return (
    <>
      <mesh position={position}>
        <sphereGeometry args={[0.1, 4, 4]} />
        <meshStandardMaterial color={color} />
      </mesh>
    </>
  )
}

function normalize(value, min = 0, max = 1) {
  return (value - min) / (max - min)
}

export const Stars = () => {
  const count = 10000
  const light = useRef()
  const mesh = useRef()
  const material = useRef()

  const particles = useMemo(() => {
    const array = []
    for (let i = 0; i < count; i++) {
      const time = random.range(0, 100)
      const factor = random.range(20, 120)
      const speed = random.range(0.01, 0.015) / 5
      const randomColor = [
        random.range(0, 1),
        random.range(0, 1),
        random.range(0, 1),
      ]

      const x = random.range(-50, 50)
      const y = random.range(-50, 50)
      const z = random.range(-50, 50)

      array.push({ time, factor, speed, randomColor, x, y, z })
    }
    return array
  }, [count])

  const object = useMemo(() => new THREE.Object3D(), [])
  const color = useMemo(() => new THREE.Color(), [])

  useFrame(() => {
    particles.forEach((particle, index) => {
      let { factor, speed, randomColor, x, y, z } = particle

      const t = (particle.time += speed)

      object.position.set(
        x + Math.cos(t / 10) + (Math.tan(t * 1) * factor) / 20,
        y + Math.sin(t / 10) + (Math.cos(t * 2) * factor) / 20,
        z + Math.cos(t / 10) + (Math.cos(t * 3) * factor) / 20
      )

      const s = Math.sin(t)
      object.scale.set(s * 0.2, s * 0.2, s * 0.2)
      object.rotation.set(s * 15, s * 15, s * 15)

      object.updateMatrix()

      // And apply the matrix to the instanced item
      mesh.current.setMatrixAt(index, object.matrix)
      //   // apply new color to instanced color
      //   material.current.setMatrixAt(index, color.set(randomColor))

      console.log(normalize(randomColor[0] * t)())

      mesh.current.setColorAt(
        index,
        color.setRGB(
          normalize(randomColor[0] * t),
          normalize(randomColor[1] * t),
          normalize(randomColor[2] * t)
        )
      )
    })
    mesh.current.instanceMatrix.needsUpdate = true
  })

  return (
    <>
      <pointLight ref={light} distance={10} intensity={5} color="hotpink" />
      <instancedMesh ref={mesh} args={[null, null, count]}>
        <dodecahedronBufferGeometry args={[10.2, 0]} />
        <meshPhongMaterial ref={material} color={color} />
      </instancedMesh>
    </>
  )
}
